import * as React from 'react';
import { FunctionComponent } from "react";
import { StringUtils } from "../utilities";
import { IconsProps, Intents, Sizes } from "./_componentsTypes";

const Icon: FunctionComponent<IconProps> = ( {icon, className, size = "sm", fill, height, title, ...props} ) => {
    const iconClassName = StringUtils.joinIgnoreEmpty( " ", "icon flex--center", `icon-${ icon.id }`, ( fill ? `fill-${ fill }` : "" ), className )

    if (!height) {
        if (size === 'xs') {
            height = 20;
        } else if (size === 'sm') {
            height = 25;
        } else if (size === 'md') {
            height = 32;
        } else if (size === 'lg') {
            height = 64;
        } else if (size === 'xl') {
            height = 100;
        }
    }

    return (
        <svg className={ iconClassName } { ...props }
             aria-labelledby={ "title" }
             xmlns="http://www.w3.org/2000/svg"
             viewBox={ icon.viewBox }
             height={ height }>
            { title && <title>{ title }</title> }
            <path d={ icon.path }/>
        </svg>
    )
}

interface IconProps {
    icon: IconsProps,
    className?: string,
    height?: number,
    size?: Sizes,
    fill?: Intents,
    title?: string
}

export default Icon;
