import * as React from "react";
import { FunctionComponent, memo, useEffect, useState } from "react";
import { CheckForScreens } from "./utilities";

const defaultState = {
    isMobile   : false,
    screen     : "desktop",
    theme      : "light",
    setTheme   : undefined,
    sideOpen   : false,
    setSideOpen: undefined
}

export const State = React.createContext( defaultState );

export const StateProvider: FunctionComponent = ( { children } ) => {
    const [ isMobile, screen ] = CheckForScreens();
    const [ theme, setTheme ] = useState( "light" );
    const [ sideOpen, setSideOpen ] = useState( isMobile );

    useEffect( () => {
        let currentTheme = localStorage.getItem( "currentTheme" );
        if (!currentTheme) {
            if (window.matchMedia && window.matchMedia( '(prefers-color-scheme: dark)' ).matches) {
                currentTheme = "dark";
            } else {
                currentTheme = "light";
            }
        }

        setTheme( currentTheme )
    }, [] )

    return (
        <State.Provider value={ {
            isMobile   : isMobile,
            screen     : screen,
            theme      : theme,
            setTheme   : setTheme,
            sideOpen   : sideOpen,
            setSideOpen: setSideOpen
        } }>
            { children }
        </State.Provider>
    )
};
