import * as React from "react";
import { FunctionComponent, useContext } from "react";
import { Link } from "gatsby";
import { evaIconsFill } from "../IconsLibs/iconsEvaFill";
import Icon from "../Icon";
import { defaultOptions } from "../Options/DefaultOptions";
import scrollTo from "gatsby-plugin-smoothscroll";
import { createLabel } from "../../utilities";
import { State } from "../../State";
import { motion } from "framer-motion"

const Sidebar: FunctionComponent = () => {
    const { sideOpen, isMobile } = useContext( State );

    return (
        <motion.aside className={ "sidebar w--sidebar bg--bgDimmed z--6 fixed left--0" }
                      animate={ { x: isMobile ? sideOpen ? 0 : "-100%" : 0} }
                      transition={ { type: 'spring', damping: 20, mass: .5 } }>
            <nav className={ "md__fixed top--header w--sidebar bg--bgDimmed" }>
                <ul className={ "sidebar__menu" }>

                    <SideLink to={ "/" } label={ "Introduction" } addPartialActive={ false }/>

                    <SideLink to={ "/customize" } label={ <span className={ "flex--center w--100 jf__cnt--between" }>Customize <Icon size={ "xs" } icon={ evaIconsFill.arrow_ios_right }/></span> }>
                        <ul className={ "sidebar__sub-menu" }>
                            <SideSubLink to={ "/customize/options" } label={ "Options" }>
                                <ul className={ "sidebar__sub-menu-lvl-2 py--xs bg--bg2 brd-y brd--body" }>
                                    {
                                        Object.keys( defaultOptions ).sort().map( ( key ) => {
                                            return (
                                                <li key={ key } className={ "sidebar__sub-item cursor--pointer" } onClick={ () => scrollTo( `#${ key }` ) }>
                                                    <div className={ "sidebar__link py--sm px--xxl" }>{ createLabel( key ) }</div>
                                                </li>
                                            )
                                        } ) }
                                </ul>
                            </SideSubLink>
                            <SideSubLink to={ "/customize/globals" } label={ "Globals" }/>
                            <SideSubLink to={ "/customize/maps" } label={ "Maps" }/>
                        </ul>
                    </SideLink>

                    <SideLink to={ "/utilities" } label={ <span className={ "flex--center w--100 jf__cnt--between" }>Utilities <Icon size={ "xs" } icon={ evaIconsFill.arrow_ios_right }/></span> }>
                        <ul className={ "sidebar__sub-menu brd-y brd--body" }>
                            <SideSubLink to={ "/utilities/align-justify" } label={ "Align / Justify" }/>
                            <SideSubLink to={ "/utilities/border" } label={ "Border" }/>
                            <SideSubLink to={ "/utilities/colors-styles" } label={ "Colors / Styles" }/>
                            <SideSubLink to={ "/utilities/cursor" } label={ "Cursor" }/>
                            <SideSubLink to={ "/utilities/flexbox" } label={ "Flexbox" }/>
                            <SideSubLink to={ "/utilities/height" } label={ "Height" }/>
                            <SideSubLink to={ "/utilities/object" } label={ "Object" }/>
                            <SideSubLink to={ "/utilities/position" } label={ "Position" }/>
                            <SideSubLink to={ "/utilities/screen-readers" } label={ "Screen Readers" }/>
                            <SideSubLink to={ "/utilities/spacers" } label={ "Spacers" }/>
                            <SideSubLink to={ "/utilities/transitions" } label={ "Transitions" }/>
                            <SideSubLink to={ "/utilities/typography" } label={ "Typography" }/>
                            <SideSubLink to={ "/utilities/width" } label={ "Width" }/>
                            <SideSubLink to={ "/utilities/z-index" } label={ "Z Index" }/>
                        </ul>
                    </SideLink>

                </ul>
            </nav>
        </motion.aside>
    )
}

export default Sidebar


interface SideLinkProps {
    to: string,
    label: string | JSX.Element,
    addPartialActive?: boolean
}


const SideLink: FunctionComponent<SideLinkProps> = ( { to, label, children, addPartialActive = true } ) => {
    return (
        <li className={ "sidebar__item" }>
            <Link getProps={ ( { isPartiallyCurrent, isCurrent } ) => {
                return {
                    className: "sidebar__link flex--center py--md px--xl" + (isCurrent ? " current" : "") +
                        (addPartialActive && isPartiallyCurrent ? " p-current bg--bg2" : "")
                }
            } }
                  to={ to }>{ label } </Link>
            { children }
        </li>
    )
}
const SideSubLink: FunctionComponent<SideLinkProps> = ( { to, label, children } ) => {
    return (
        <li className={ "sidebar__sub-item" }>
            <Link activeClassName={ "current bg--bg2" } className={ "sidebar__link flex--center py--md px--xl" } to={ to }>{ label }</Link>
            { children }
        </li>
    )
}
